* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Montserrat', 'Arial', sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.2;
  color: #000;
}

/* Global classes */

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.clearfix:before,
.clearfix:after {
  content: '';
  display: table;
  clear: both;
}

.clear {
  clear: both;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  overflow: hidden;
  margin-top: -1px;
  margin-left: -1px;
  position: absolute;
  clip: rect(0, 0, 0, 0);
  border: none;
}

.btn {
  background-color: #5b815d;
  width: 213px;
  height: 57px;
  line-height: 57px;
  display: inline-block;
  text-align: center;
  color: #fffefe;
  font-weight: 600;
  border: none;
  text-decoration: none;
  border-radius: 29px;
  box-shadow: 0px 7px 21px 0px rgba(55, 125, 60, 0.3);
}

/* Atomic classes */

.d-n {
  display: none;
}

.d-b {
  display: block;
}

/* General */

a:focus,
a:active,
button:focus,
button:active {
  outline-color: #97c89a;
}

.header-navigation {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding-top: 48px;
  min-height: 100px;
}

.header .menu {
  flex-grow: 1;
  display: flex;
  justify-content: flex-end;
  list-style: none;
}

.header .menu.isOpened {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.9);
}

.header .navigation__a {
  position: relative;
  text-decoration: none;
  color: black;
  margin: 0 0 6px 20px;
  font-size: 14px;
  font-weight: 400;
}

.header .navigation__a:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  border-bottom: 1px solid #97c89a;
  transition: width 0.5s;
}

.header .navigation__a:hover:after {
  width: 100%;
}

.header .navigation__a:nth-child(1) {
  padding: 0;
}

.header .navigation__a:nth-child(5) {
  margin: 0 0 0 53px;
}

.mobile-menu {
  display: none;
  background-color: transparent;
  border: 2px solid black;
  border-right-color: transparent;
  border-left-color: transparent;
  width: 30px;
  height: 20px;
  position: relative;
  transition: 0.3s;
  outline: none;
}

.mobile-menu.closer {
  position: fixed;
  z-index: 5;
  top: 20px;
  right: 20px;
}

.mobile-menu:before {
  content: '';
  display: block;
  height: 2px;
  border-bottom: 2px solid black;
  position: absolute;
  right: 0;
  top: 50%;
  left: 0;
  margin-top: -3px;
  transition: 0.3s;
  outline: none;
}

.mobile-menu:hover,
.mobile-menu:focus,
.mobile-menu:active {
  border-top-color: #97c89a;
  border-bottom-color: #97c89a;
  transform: scaleY(1.3);
}

.mobile-menu:hover:before,
.mobile-menu:active:before,
.mobile-menu:focus:before {
  border-bottom-color: #97c89a;
}

.main {
  border-top: 1px;
}

.first-screen {
  padding-top: 185px;
  padding-bottom: 266px;
  margin-bottom: 183px;
  background: url(../img/Fon.jpg) no-repeat;
  background-size: cover;
  min-height: 100vh;
}

.slogan__text {
  font-size: 4.69vw;
  width: calc(58.333333333333336% - 20px);
  margin-bottom: 55px;
}

.slogan__btn {
  transition: background-color 0.3s;
  border: 2px solid transparent;
}

.slogan__btn:hover {
  background-color: #5b815d;
  color: #fff;
}

.slogan__btn_white {
  margin-left: 25px;
  border: 2px solid #5b815d;
  background-color: transparent;
  color: #000;
  box-shadow: unset;
}

.slogan__btn_white:hover {
  background-color: #5b815d;
  border: none;
}

.product-range__h {
  margin-bottom: 43px;
  font-size: 48px;
  text-align: center;
  font-weight: 400;
}

.product-range__p {
  width: 50%;
  margin: 0 auto;
  margin-bottom: 60px;
  font-size: 14px;
  line-height: 21px;
  color: #555;
  text-align: center;
}

.product-tab {
  display: flex;
  justify-content: center;
  align-items: baseline;
  margin-bottom: 35px;
}

.product-tab__li {
  font-weight: 600;
  font-size: 14px;
  list-style: none;
  padding: 2px 5px;
  border-bottom: 2px solid transparent;
  transition: 0.5s;
}

.product-tab__li + .product-tab__li {
  margin-left: 65px;
}

.product-tab__li.active,
.product-tab__li:hover,
.product-tab__li:focus,
.product-tab__li:active {
  border-bottom: 2px solid #97c89a;
}

.product-tab__li.active a,
.product-tab__li:hover a,
.product-tab__li:focus a,
.product-tab__li:active a {
  color: green;
}

.product-tab__link {
  text-decoration: none;
  color: #000000;
}

.product-tab__link:hover,
.product-tab__link:focus,
.product-tab__link:active {
  color: #5b815d;
}

.product-content {
  list-style: none;
  margin-bottom: 170px;
}

.product-content__li {
  margin-bottom: 170px;
  display: none;
}

.active {
  display: flex;
}

.product {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(25% - 20px);
  list-style: none;
  text-align: center;
}

.product__img {
  width: 100%;
  height: auto;
  margin-bottom: 30px;
  background-size: cover;
}

.product__text {
  margin-bottom: 17px;
  font-size: 16px;
  font-weight: 600;
  color: #010101;
}

.product__price {
  margin-bottom: 30px;
}

.info {
  margin-bottom: 227px;
}

.info__h {
  font-size: 48px;
  font-weight: 400;
  margin-bottom: 60px;
}

.info__p {
  margin-bottom: 40px;
  color: #000;
  font-size: 14px;
  line-height: 21px;
}

.info__btn {
  border: none;
  border-bottom: 1px solid #97c89a;
  color: #5b815d;
  font-size: 16px;
  font-weight: 600;
  background-color: transparent;
}

.slider-container {
  float: left;
  margin-right: 20px;
  text-align: right;
  margin-bottom: 40px;
}

.slider-container__btn {
  border: transparent;
  background-color: transparent;
  height: 14px;
  width: 21px;
  position: relative;
}

.slider-container__btn_next {
  margin-left: 10px;
}

.slider-container__btn_back {
  transform: rotate(180deg);
}

.slider-container__btn:before {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-image: url(../img/ico-arrow.svg);
  background-repeat: no-repeat;
}

.gradient {
  margin-top: 184px;
  padding-bottom: 180px;
}

.feedback_colortext {
  font-weight: 400;
  text-align: center;
  color: #000;
  margin-bottom: 80px;
  font-size: 60px;
}

.my-row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.feedback-card {
  background-color: #ffffff;
  border: 1px solid #ececec;
  border-radius: 10px;
  margin-bottom: 45px;
  padding-top: 50px;
  padding: 0 33px;
  padding-bottom: 75px;
  box-shadow: 2px 3px 3px #ececec;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(33.333333333333336% - 20px);
}

.media(360px,
 {
  min-height: 600px;
}

) .feedback-card__face {
  margin: 68px 0;
}

.feedback-card__text {
  margin: 24px 40px 0 0;
  font-weight: 500;
  margin-bottom: 36px;
}

.screen {
  height: 100%;
}

.features {
  text-align: center;
  margin-bottom: 36px;
}

.centerP {
  text-align: center;
  width: 50%;
  margin: 0 auto;
  margin-bottom: 46px;
}

.advantages {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.advantages-li {
  list-style-type: none;
  padding-top: 115px;
  text-align: center;
  padding-bottom: 14px;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(25% - 20px);
  position: relative;
}

.advantages-li + li {
  border-left: 1px solid #dddddd;
}

.advantages-li:before {
  content: '';
  background-image: url(../img/noun_Coffee.png);
  background-color: #eaf4eb;
  background-repeat: no-repeat;
  background-position: center;
  width: 55px;
  height: 55px;
  position: absolute;
  top: 40%;
  left: 50%;
  margin-top: -27.5px;
  margin-left: -27.5px;
  border-radius: 50%;
}

.advantages-li__Tasty:before {
  background-image: url(../img/noun_muffin.png);
}

.advantages-li__WiFi:before {
  background-image: url(../img/noun_wifi.png);
}

.advantages-li__Comfortable:before {
  background-image: url(../img/noun_Sofa.png);
}

.contacts {
  display: flex;
  align-items: center;
  padding-bottom: 50px;
}

.contacts-info {
  width: calc(33.333333333333336% - 20px);
}

.contacts-info__h {
  font-size: 48px;
  font-weight: 400;
  margin-bottom: 70px;
}

.contacts-info__p {
  margin-bottom: 25px;
  font-size: 16px;
  font-weight: 400;
}

.contacts-info__p:before {
  content: '';
  display: inline-block;
  width: 15px;
  height: 15px;
  background-size: contain;
  background-repeat: no-repeat;
}

.contacts-info__time:before {
  background-image: url(../img/ico_time.png);
}

.contacts-info__adress:before {
  background-image: url(../img/ico-adress.png);
}

.contacts-info__phone:before {
  background-image: url(../img/ico-phone.png);
}

.contacts-info__email:before {
  background-image: url(../img/ico-email.png);
  transform: translateY(5px);
}

.contacts .contact-info__link,
.contacts:active,
.contacts:visited {
  text-decoration: none;
  color: black;
}

.contacts-map {
  width: calc(66.66666666666667% - 20px);
}

.footer-copyright {
  text-align: center;
  margin-bottom: 46px;
  color: #555555;
  font-size: 14px;
}

@media screen and (max-width: 960px) {
  .product {
    width: calc(33.333333333333336% - 20px);
  }

  .feedback-card {
    width: calc(50% - 20px);
  }

  .advantages-li {
    width: calc(50% - 20px);
  }
}

@media screen and (max-width: 950px) {
  .contacts {
    display: block;
  }

  .contacts-info {
    width: 100%;
    margin: 0 auto;
  }

  .contacts-info__h {
    width: 100%;
    text-align: center;
    margin: 0 auto;
  }

  .contacts-map {
    width: 100%;
  }
}

@media screen and (max-width: 939px) {
  .info__h {
    font-size: 2.5rem;
    text-align: center;
  }

  .slider-container {
    float: none;
    text-align: center;
    margin: 0 auto;
  }
}

@media screen and (max-width: 839px) {
  .product-content {
    margin-bottom: 10px;
  }

  .active {
    margin-bottom: 10px;
  }
}

@media screen and (max-width: 780px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  .product {
    width: calc(50% - 20px);
  }

  .feedback-card {
    width: calc(100% - 20px);
  }

  .advantages-li {
    width: calc(50% - 20px);
  }
}

@media screen and (max-width: 730px) {
  .slogan__text {
    font-size: 35px;
  }

  .gradient {
    padding: 0;
  }

  .feedback_colortext {
    font-size: 35px;
  }
}

@media screen and (max-width: 700px) {
  .header .menu {
    display: none;
  }

  .header .navigation__a {
    margin-bottom: 30px;
    color: white;
    text-align: center;
    font-size: 30px;
  }

  .mobile-menu {
    display: block;
  }
}

@media screen and (max-width: 561px) {
  .product {
    margin: 0 auto;
  }
}

@media screen and (max-width: 560px) {
  .product {
    width: calc(75% - 20px);
  }

  .feedback-card {
    width: calc(100% - 20px);
  }

  .advantages-li {
    width: calc(100% - 20px);
  }
}

@media screen and (max-width: 490px) {
  .slogan__btn {
    display: block;
    margin: 0 auto;
    margin-top: 20px;
  }
}

@media screen and (max-width: 468px) {
  .first-screen {
    background-position: -300px;
  }

  .slogan__text {
    font-size: 40px;
    width: calc(100% - 20px);
    margin-bottom: 80px;
  }

  .gradient {
    padding: 0;
  }

  .feedback_colortext {
    margin: 0 auto;
    padding-top: -200px;
    font-size: 40px;
    width: calc(100% - 20px);
  }
}